.up {
  position: fixed;
  z-index: 10;
  color: $white;
  bottom: 3rem;
  right: 9.6rem;
  width: 5rem;
  height: 5rem;
  cursor: pointer;
  display: none;


  &:hover {
    svg {
      fill: $purple;
    }
  }

  &--shown {
    display: flex;
  }

  & svg {
    transition: 0.3s ease-in-out;
    fill: $white;
  }

  &:hover {
    color: $purple;
  }

  @media (max-width: $mobile) {
    bottom: 30px;
    right: 20px;
    width: 25px;
    height: 25px;

    &:hover {
      color: $white;
      width: 25px;
      height: 25px;

      svg {
        fill: $white;
      }

    }
  }

}
